export default {
	data() {
		return {
			title: 'Hello',
			backpage:'/pages/index/index',
			backtype:'redirectTo'
		}
	},
	onLoad(options) {
		console.log(options)
		
	},
	onReady(){

	},
	methods: {
		formSubmit:function(e){
			var t = this;
			uni.request({
				url: this.apiServer+'/staff/staff/login',
				method: 'POST',
				data: e.detail.value,
				success: res => {
					let data = res.data;
					if(data.err>0){
						uni.showToast({
							title: data.msg
						});
					}else{
						uni.setStorageSync('STID',data.data.st_id);
						uni.setStorageSync('NAME',data.data.name);
						uni.setStorageSync('IPHONE',data.data.iphone);
						uni.setStorageSync('NICKNAME',data.data.nickname);
						uni.reLaunch({
							url: t.backpage
						});
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		go_forget(){
			uni.navigateTo({
			    url: 'forget'
			})
		},
		go_register(){
			uni.navigateTo({
				url: '../../pages/register/register'
			})
		}
		
	}
}
